/**
 * Valida RUC segn formato oficial del SRI Ecuador
 * 
 * Formatos vlidos:
 * 1. Personas naturales ecuatorianas y extranjeros residentes:
 *    - [Cdula 10 dgitos] + 001
 *    - Tercer dgito: 0-5
 * 
 * 2. Sociedades privadas y extranjeros no residentes (sin cdula):
 *    - [Cdigo provincia (2)] + 9 + [Dgitos consecutivos (7)] + 001
 *    - Tercer dgito: 9
 * 
 * 3. Sociedades pblicas:
 *    - [Cdigo provincia (2)] + [6 o 9] + [Dgitos consecutivos] + 0001
 *    - Tercer dgito: 6 o 9
 */
export function validateRucEcuador(input: string): boolean {
  if (!input) return false;
  
  // Limpiar y validar solo dgitos
  const ruc = input.replace(/\D/g, '');
  
  // Aceptamos cdula de 10 dgitos o RUC completo de 13 dgitos
  if (ruc.length !== 10 && ruc.length !== 13) return false;
  
  // Validar cdigo de provincia (01-24)
  const province = parseInt(ruc.substring(0, 2), 10);
  if (province < 1 || province > 24) return false;
  
  const thirdDigit = parseInt(ruc.charAt(2), 10);
  
  // Funcin para validar cdula (primeros 10 dgitos de persona natural)
  const validateCedula = (cedula: string): boolean => {
    if (cedula.length !== 10) return false;
    
    const tercerDigito = parseInt(cedula.charAt(2), 10);
    if (tercerDigito > 5) return false; // Persona natural: 0-5
    
    const coeff = [2, 1, 2, 1, 2, 1, 2, 1, 2];
    let sum = 0;
    
    for (let i = 0; i < 9; i++) {
      let v = parseInt(cedula.charAt(i), 10) * coeff[i];
      if (v >= 10) v -= 9;
      sum += v;
    }
    
    const mod = sum % 10;
    const check = mod === 0 ? 0 : 10 - mod;
    return check === parseInt(cedula.charAt(9), 10);
  };
  
  // Funcin para validar RUC de sociedad privada (tercer dgito = 9)
  const validatePrivateCompany = (r: string): boolean => {
    if (r.length !== 13) return false;
    
    // Tercer dgito debe ser 9
    if (r.charAt(2) !== '9') return false;
    
    // Los ltimos 3 dgitos deben ser 001
    if (r.substring(10) !== '001') return false;
    
    const coeff = [4, 3, 2, 7, 6, 5, 4, 3, 2];
    let sum = 0;
    
    for (let i = 0; i < 9; i++) {
      sum += parseInt(r.charAt(i), 10) * coeff[i];
    }
    
    const mod = sum % 11;
    const check = mod === 0 ? 0 : 11 - mod;
    
    // Si el dgito verificador es 10, el RUC es invlido
    if (check === 10) return false;
    
    return check === parseInt(r.charAt(9), 10);
  };
  
  // Funcin para validar RUC de sociedad pblica (tercer dgito = 6 o 9)
  const validatePublicCompany = (r: string): boolean => {
    if (r.length !== 13) return false;
    
    // Tercer dgito debe ser 6 o 9
    const tercerDigito = r.charAt(2);
    if (tercerDigito !== '6' && tercerDigito !== '9') return false;
    
    // Los ltimos 4 dgitos deben ser 0001
    if (r.substring(9) !== '0001') return false;
    
    const coeff = [3, 2, 7, 6, 5, 4, 3, 2];
    let sum = 0;
    
    for (let i = 0; i < 8; i++) {
      sum += parseInt(r.charAt(i), 10) * coeff[i];
    }
    
    const mod = sum % 11;
    const check = mod === 0 ? 0 : 11 - mod;
    
    // Si el dgito verificador es 10, el RUC es invlido
    if (check === 10) return false;
    
    return check === parseInt(r.charAt(8), 10);
  };
  
  // Personas naturales ecuatorianas y extranjeros residentes (tercer dgito 0-5)
  if (thirdDigit >= 0 && thirdDigit <= 5) {
    // Los primeros 10 dgitos deben formar una cdula vlida
    const cedula = ruc.substring(0, 10);
    if (!validateCedula(cedula)) return false;
    // Si el usuario ingres solo la cdula (10 dgitos), consideramos vlido
    if (ruc.length === 10) return true;
    // Si es RUC completo de 13 dgitos, los ltimos 3 dgitos deben ser 001
    return ruc.substring(10) === '001';
  }
  
  // Sociedades pblicas (tercer dgito 6)
  if (thirdDigit === 6) {
    return validatePublicCompany(ruc);
  }
  
  // Sociedades privadas y extranjeros no residentes (tercer dgito 9)
  if (thirdDigit === 9) {
    // Verificar si es sociedad privada (termina en 001) o sociedad pblica (termina en 0001)
    if (ruc.substring(10) === '001') {
      return validatePrivateCompany(ruc);
    } else if (ruc.substring(9) === '0001') {
      return validatePublicCompany(ruc);
    }
    return false;
  }
  
  // Tercer dgito no vlido (7, 8 no estn permitidos)
  return false;
}

/**
 * Obtiene el tipo de RUC segn el formato
 */
export function getTipoRuc(ruc: string): string {
  if (!ruc) return 'Invlido';
  const cleaned = ruc.replace(/\D/g, '');
  if (cleaned.length !== 10 && cleaned.length !== 13) return 'Invlido';
  const thirdDigit = parseInt(cleaned.charAt(2), 10);
  
  if (thirdDigit >= 0 && thirdDigit <= 5) {
    return 'Persona Natural';
  }
  
  if (thirdDigit === 6) {
    return 'Sociedad Pblica';
  }
  
  if (thirdDigit === 9) {
    if (ruc.substring(10) === '001') {
      return 'Sociedad Privada';
    } else if (ruc.substring(9) === '0001') {
      return 'Sociedad Pblica';
    }
  }
  
  return 'Invlido';
}
