import React from 'react';
import './Emisores.css'; // Reutilizar estilos de Emisores
import { planesApi, Plan } from '../services/planesApi';
import PlanFormModal from './PlanFormModal';
import PlanDeleteModal from './PlanDeleteModal';
import { useNotification } from '../contexts/NotificationContext';
import { useUser } from '../contexts/userContext';
import LoadingSpinner from '../components/LoadingSpinner';

interface ListResponse {
  data: Plan[];
  pagination: {
    total: number;
    per_page: number;
    current_page: number;
    last_page: number;
    from: number;
    to: number;
  };
}

type SortField = 'nombre' | 'precio' | 'periodo' | 'estado' | 'created_at';
type SortDirection = 'asc' | 'desc';

const Planes: React.FC = () => {
  const [planes, setPlanes] = React.useState<Plan[]>([]);
  const [loading, setLoading] = React.useState(false);
  const [currentPage, setCurrentPage] = React.useState(1);
  const [itemsPerPage, setItemsPerPage] = React.useState(10);
  const [totalItems, setTotalItems] = React.useState(0);
  const { show } = useNotification();
  const { user: currentUser } = useUser();
  const [openNew, setOpenNew] = React.useState(false);
  const [openEdit, setOpenEdit] = React.useState(false);
  const [editingPlan, setEditingPlan] = React.useState<Plan | null>(null);
  const [openDelete, setOpenDelete] = React.useState(false);
  const [deletingPlan, setDeletingPlan] = React.useState<Plan | null>(null);
  const [sortField, setSortField] = React.useState<SortField>('created_at');
  const [sortDirection, setSortDirection] = React.useState<SortDirection>('desc');
  const [showFilters, setShowFilters] = React.useState(false);
  
  // Filtros avanzados
  const [filters, setFilters] = React.useState({
    nombre: '',
    cantidad_comprobantes_gte: '',
    precio: '',
    periodo: '',
    observacion: '',
    estado: '',
    createdFrom: '',
    createdTo: '',
    updatedFrom: '',
    updatedTo: '',
  });

  // Verificar que el usuario sea administrador
  const isAdmin = currentUser?.role === 'administrador';

  // Cargar planes
  const loadPlanes = React.useCallback(async () => {
    try {
      setLoading(true);
      const params: Record<string, any> = {
        page: currentPage,
        per_page: itemsPerPage,
        sort_by: sortField,
        sort_dir: sortDirection,
      };
      
      // Aplicar todos los filtros
      if (filters.nombre) params.nombre = filters.nombre;
      if (filters.cantidad_comprobantes_gte) params.cantidad_comprobantes_gte = filters.cantidad_comprobantes_gte;
      if (filters.precio) params.precio = filters.precio;
      if (filters.periodo) params.periodo = filters.periodo;
      if (filters.observacion) params.observacion = filters.observacion;
      if (filters.estado) params.estado = filters.estado;
      if (filters.createdFrom) params.created_at_from = filters.createdFrom;
      if (filters.createdTo) params.created_at_to = filters.createdTo;
      if (filters.updatedFrom) params.updated_at_from = filters.updatedFrom;
      if (filters.updatedTo) params.updated_at_to = filters.updatedTo;
      
      const response = await planesApi.list(params);
      const data = response.data as ListResponse;
      setPlanes(data.data);
      setTotalItems(data.pagination.total);
    } catch (err: any) {
      const msg = err?.response?.data?.message || err?.message || 'Error cargando planes';
      show({ title: 'Error', message: msg, type: 'error' });
    } finally {
      setLoading(false);
    }
  }, [currentPage, itemsPerPage, filters, sortField, sortDirection, show]);

  // Cargar en el primer render y cuando cambien los filtros
  React.useEffect(() => {
    loadPlanes();
  }, [loadPlanes]);

  // Manejar cambio de ordenamiento
  const handleSort = (field: SortField) => {
    if (sortField === field) {
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortDirection('asc');
    }
  };

  // Manejar edicin
  const handleEdit = (plan: Plan) => {
    setEditingPlan(plan);
    setOpenEdit(true);
  };

  // Manejar eliminacin
  const handleDelete = (plan: Plan) => {
    setDeletingPlan(plan);
    setOpenDelete(true);
  };

  // Renderizar cono de ordenamiento
  const renderSortIcon = (field: SortField) => {
    if (sortField !== field) return null;
    return sortDirection === 'asc' ? ' ' : ' ';
  };

  // Formatear precio
  const formatPrecio = (precio: number) => {
    return new Intl.NumberFormat('es-EC', {
      style: 'currency',
      currency: 'USD',
    }).format(precio);
  };

  // Formatear fecha
  const formatDate = (dateString?: string) => {
    if (!dateString) return '-';
    const date = new Date(dateString);
    return date.toLocaleDateString('es-EC', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
    });
  };

  // Calcular rango de pginas
  const totalPages = Math.ceil(totalItems / itemsPerPage);

  if (!isAdmin) {
    return (
      <div className="emisores-container">
        <div className="content-header">
          <h1>Acceso Denegado</h1>
          <p>No tienes permisos para acceder a esta seccin.</p>
        </div>
      </div>
    );
  }

  return (
    <div className="emisores-container">
      <div className="content-header">
        <div className="header-top">
          <div className="title-section">
            <h1>Gestin de Planes</h1>
            <p className="subtitle">Administra los planes de facturacin del sistema</p>
          </div>
          <button className="btn-primary" onClick={() => setOpenNew(true)}>
            <span className="icon">+</span>
            Nuevo Plan
          </button>
        </div>

        <div style={{ marginBottom: '20px' }}>
          {/* Botn Toggle para Filtros */}
          <button
            className="btn-primary"
            onClick={() => setShowFilters(!showFilters)}
            style={{
              background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
              border: 'none',
              color: 'white',
              padding: '12px 24px',
              borderRadius: '12px',
              fontSize: '14px',
              fontWeight: 600,
              cursor: 'pointer',
              marginBottom: showFilters ? '20px' : '0',
              display: 'flex',
              alignItems: 'center',
              gap: '8px',
              transition: 'all 0.3s ease',
            }}
          >
            <span></span>
            {showFilters ? 'Ocultar Filtros' : 'Filtros'}
          </button>

          {/* Panel de Filtros */}
          {showFilters && (
            <div
              style={{
                background: 'linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%)',
                border: '2px solid #667eea',
                borderRadius: '12px',
                padding: '20px',
                marginBottom: '20px',
                boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
              }}
            >
              <div
                style={{
                  display: 'grid',
                  gridTemplateColumns: 'repeat(auto-fit, minmax(250px, 1fr))',
                  gap: '16px',
                  marginBottom: '16px',
                }}
              >
                {/* Filtro: Nombre */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Nombre
                  </label>
                  <input
                    type="text"
                    placeholder="Buscar por nombre..."
                    value={filters.nombre}
                    onChange={(e) => {
                      setFilters({ ...filters, nombre: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Cantidad de Comprobantes */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Cantidad Comprobantes ()
                  </label>
                  <input
                    type="number"
                    placeholder="Cantidad mnima..."
                    value={filters.cantidad_comprobantes_gte}
                    onChange={(e) => {
                      setFilters({ ...filters, cantidad_comprobantes_gte: e.target.value });
                      setCurrentPage(1);
                    }}
                    min="0"
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Precio */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Precio
                  </label>
                  <input
                    type="number"
                    placeholder="Precio..."
                    value={filters.precio}
                    onChange={(e) => {
                      setFilters({ ...filters, precio: e.target.value });
                      setCurrentPage(1);
                    }}
                    min="0"
                    step="0.01"
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Perodo */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Perodo
                  </label>
                  <select
                    value={filters.periodo}
                    onChange={(e) => {
                      setFilters({ ...filters, periodo: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  >
                    <option value="">Todos</option>
                    <option value="Mensual">Mensual</option>
                    <option value="Trimestral">Trimestral</option>
                    <option value="Semestral">Semestral</option>
                    <option value="Anual">Anual</option>
                    <option value="Bianual">Bianual</option>
                    <option value="Trianual">Trianual</option>
                  </select>
                </div>

                {/* Filtro: Observacin */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Observacin
                  </label>
                  <input
                    type="text"
                    placeholder="Buscar por observacin..."
                    value={filters.observacion}
                    onChange={(e) => {
                      setFilters({ ...filters, observacion: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Estado */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Estado
                  </label>
                  <select
                    value={filters.estado}
                    onChange={(e) => {
                      setFilters({ ...filters, estado: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  >
                    <option value="">Todos</option>
                    <option value="Activo">Activo</option>
                    <option value="Desactivado">Desactivado</option>
                  </select>
                </div>

                {/* Filtro: Fecha Creacin (Desde) */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Fecha Creacin (Desde)
                  </label>
                  <input
                    type="date"
                    value={filters.createdFrom}
                    onChange={(e) => {
                      setFilters({ ...filters, createdFrom: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Fecha Creacin (Hasta) */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Fecha Creacin (Hasta)
                  </label>
                  <input
                    type="date"
                    value={filters.createdTo}
                    onChange={(e) => {
                      setFilters({ ...filters, createdTo: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Fecha Actualizacin (Desde) */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Fecha Actualizacin (Desde)
                  </label>
                  <input
                    type="date"
                    value={filters.updatedFrom}
                    onChange={(e) => {
                      setFilters({ ...filters, updatedFrom: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>

                {/* Filtro: Fecha Actualizacin (Hasta) */}
                <div>
                  <label style={{ fontSize: '13px', fontWeight: 600, color: '#374151', marginBottom: '6px', display: 'block' }}>
                    Fecha Actualizacin (Hasta)
                  </label>
                  <input
                    type="date"
                    value={filters.updatedTo}
                    onChange={(e) => {
                      setFilters({ ...filters, updatedTo: e.target.value });
                      setCurrentPage(1);
                    }}
                    style={{
                      width: '100%',
                      padding: '8px 12px',
                      borderRadius: '6px',
                      border: '1px solid #d1d5db',
                      fontSize: '14px',
                      boxSizing: 'border-box',
                    }}
                  />
                </div>
              </div>

              {/* Botn Limpiar Filtros */}
              <div style={{ textAlign: 'right' }}>
                <button
                  onClick={() => {
                    setFilters({
                      nombre: '',
                      cantidad_comprobantes_gte: '',
                      precio: '',
                      periodo: '',
                      observacion: '',
                      estado: '',
                      createdFrom: '',
                      createdTo: '',
                      updatedFrom: '',
                      updatedTo: '',
                    });
                    setCurrentPage(1);
                  }}
                  style={{
                    background: 'linear-gradient(135deg, #f93b1d 0%, #ea1e63 100%)',
                    border: 'none',
                    color: 'white',
                    padding: '10px 20px',
                    borderRadius: '6px',
                    fontSize: '14px',
                    fontWeight: 600,
                    cursor: 'pointer',
                    transition: 'all 0.3s ease',
                  }}
                >
                   Limpiar Filtros
                </button>
              </div>
            </div>
          )}

          {/* Selector de items por pgina */}
          <div style={{ display: 'flex', gap: '12px', alignItems: 'center', marginBottom: '16px' }}>
            <label style={{ fontSize: '14px', fontWeight: 600 }}>Mostrar:</label>
            <select
              value={itemsPerPage}
              onChange={(e) => {
                setItemsPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              style={{
                padding: '8px 12px',
                borderRadius: '6px',
                border: '1px solid #d1d5db',
                fontSize: '14px',
              }}
            >
              <option value="10">10 por pgina</option>
              <option value="25">25 por pgina</option>
              <option value="50">50 por pgina</option>
              <option value="100">100 por pgina</option>
            </select>
          </div>
        </div>
      </div>

      {loading ? (
        <LoadingSpinner message="Cargando planes..." />
      ) : (
        <>
          <div className="table-container">
            <table className="data-table">
              <thead>
                <tr>
                  <th onClick={() => handleSort('nombre')} style={{ cursor: 'pointer' }}>
                    Nombre {renderSortIcon('nombre')}
                  </th>
                  <th>Cantidad de comprobantes</th>
                  <th onClick={() => handleSort('precio')} style={{ cursor: 'pointer' }}>
                    Precio {renderSortIcon('precio')}
                  </th>
                  <th onClick={() => handleSort('periodo')} style={{ cursor: 'pointer' }}>
                    Perodo {renderSortIcon('periodo')}
                  </th>
                  <th>Colores</th>
                  <th>Observacin</th>
                  <th onClick={() => handleSort('estado')} style={{ cursor: 'pointer' }}>
                    Estado {renderSortIcon('estado')}
                  </th>
                  <th>Comprobantes mnimos</th>
                  <th>Das mnimos</th>
                  <th onClick={() => handleSort('created_at')} style={{ cursor: 'pointer' }}>
                    Fecha de creacin {renderSortIcon('created_at')}
                  </th>
                  <th>Fecha de actualizacin</th>
                  <th className="actions-column">Acciones</th>
                </tr>
              </thead>
              <tbody>
                {planes.length === 0 ? (
                  <tr>
                    <td colSpan={12} style={{ textAlign: 'center', padding: '2rem' }}>
                      {filters.nombre || filters.cantidad_comprobantes_gte || filters.precio || filters.periodo || filters.observacion || filters.estado || filters.createdFrom || filters.createdTo || filters.updatedFrom || filters.updatedTo
                        ? 'No se encontraron planes con los filtros aplicados'
                        : 'No hay planes registrados'}
                    </td>
                  </tr>
                ) : (
                  planes.map((plan) => (
                    <tr key={plan.id}>
                      <td>
                        <strong>{plan.nombre}</strong>
                      </td>
                      <td>{plan.cantidad_comprobantes.toLocaleString()}</td>
                      <td>
                        <strong>{formatPrecio(plan.precio)}</strong>
                      </td>
                      <td>{plan.periodo}</td>
                      <td>
                        <div style={{ display: 'flex', gap: '8px', alignItems: 'center' }}>
                          <div
                            style={{
                              width: '30px',
                              height: '30px',
                              backgroundColor: plan.color_fondo,
                              border: '1px solid #ddd',
                              borderRadius: '4px',
                            }}
                            title={`Fondo: ${plan.color_fondo}`}
                          />
                          <div
                            style={{
                              width: '30px',
                              height: '30px',
                              backgroundColor: plan.color_texto,
                              border: '1px solid #ddd',
                              borderRadius: '4px',
                            }}
                            title={`Texto: ${plan.color_texto}`}
                          />
                        </div>
                      </td>
                      <td>
                        {plan.observacion
                          ? (plan.observacion.length > 80
                              ? `${plan.observacion.substring(0, 80)}...`
                              : plan.observacion)
                          : '-'}
                      </td>
                      <td>
                        <span className={`badge ${plan.estado === 'Activo' ? 'badge-success' : 'badge-danger'}`}>
                          {plan.estado}
                        </span>
                      </td>
                      <td>{plan.comprobantes_minimos}</td>
                      <td>{plan.dias_minimos}</td>
                      <td>{formatDate(plan.created_at)}</td>
                      <td>{formatDate(plan.updated_at)}</td>
                      <td className="actions-cell">
                        <button className="btn-icon btn-edit" onClick={() => handleEdit(plan)} title="Editar plan">
                          
                        </button>
                        <button className="btn-icon btn-delete" onClick={() => handleDelete(plan)} title="Eliminar plan">
                          
                        </button>
                      </td>
                    </tr>
                  ))
                )}
              </tbody>
            </table>
          </div>

          {totalPages > 1 && (
            <div className="pagination">
              <button
                onClick={() => setCurrentPage(currentPage - 1)}
                disabled={currentPage === 1}
                className="pagination-btn"
              >
                Anterior
              </button>
              <span className="pagination-info">
                Pgina {currentPage} de {totalPages} ({totalItems} planes en total)
              </span>
              <button
                onClick={() => setCurrentPage(currentPage + 1)}
                disabled={currentPage >= totalPages}
                className="pagination-btn"
              >
                Siguiente
              </button>
            </div>
          )}
        </>
      )}

      {openNew && (
        <PlanFormModal
          open={openNew}
          onClose={() => setOpenNew(false)}
          onSuccess={() => {
            setOpenNew(false);
            loadPlanes();
          }}
        />
      )}

      {openEdit && editingPlan && (
        <PlanFormModal
          open={openEdit}
          onClose={() => {
            setOpenEdit(false);
            setEditingPlan(null);
          }}
          onSuccess={() => {
            setOpenEdit(false);
            setEditingPlan(null);
            loadPlanes();
          }}
          plan={editingPlan}
        />
      )}

      {openDelete && deletingPlan && (
        <PlanDeleteModal
          open={openDelete}
          plan={deletingPlan}
          onClose={() => {
            setOpenDelete(false);
            setDeletingPlan(null);
          }}
          onSuccess={() => {
            setOpenDelete(false);
            setDeletingPlan(null);
            loadPlanes();
          }}
        />
      )}
    </div>
  );
};

export default Planes;
